*{
      margin: 0;
      padding: 0;
}

.home {
      padding: 100px;
      
}

.h-wrap {
      border-bottom: 1px solid #000;
}

.l-items {
      border-top: 1px black solid;
      border-left: 1px solid black;
      display: flex;
      justify-content: space-between;
      /* align-content: center; */
      width: 100%;
      line-height: 40px;
}

.l-items span {
      display: inline-block;
      border-right: 1px solid black;
}

.i-name, .i-price, .i-add {
      width: 15%;
      text-align: center;
}

.i-infor {
      text-indent: 15px;
      width: 55%;
      font-size: 14px;
}

.shop {
      margin-top: 50px;
}

.shop h2{
      text-align: center;
      line-height: 80px;
}

.shop .s-items {
      display: flex;
      line-height: 40px;
      justify-content: space-around;
}

.shop .s-items button {
      width: 8%;
      border: none;
      background-color: white;
}

.s-items span {
      display: inline-block;
}

.s-name, .s-num, .s-total {
      width: 12%;
      text-align: center;
}